import { Typography, Box, Stack } from "@mui/material";
import { purple } from "../../common/Constants";
import yutoAb from "../../../../assets/2024_.png";
import { Sns } from "../../common/Sns";

export const Top = () => {
  return (
    <Box
      sx={{
        backgroundColor: purple,
        color: "white",
        minHeight: "100vh",
        display: "flex",
        alignItems: "center",
        // justifyContent: "center",
      }}
    >
      <Stack spacing={4} sx={{ alignItems: "center" }}>
        <Stack direction="row" spacing={3}>
          <Box
            component="img"
            src={yutoAb}
            alt="yutoAb"
            sx={{
              width: "40%",
              height: "auto",
              objectFit: "contain",
            }}
          />
          <Stack spacing={3}>
            <Typography variant="h4">
              Yuto Abe <br />  
            </Typography>
            <Sns />
          </Stack>
        </Stack>
        <Box
          sx={{
            maxWidth: "350px", // 
            width: "100%", // 
            mx: "auto", // 
          }}
        >
          <div>  </div>
          <br />
          <div>
            
            
          </div>
          <div>
            
          </div>
          <br />
          <div>
            WEB
          </div>
          <div>
             ReactTypeScript Scala
          </div>
          <br />
          <div></div>
          <div>
            
          </div>
        </Box>
      </Stack>
    </Box>
  );
};
