import { Typography, Box, Stack, useMediaQuery } from "@mui/material";
import { useTheme } from "@mui/material/styles";
import { purple } from "../../common/Constants";
import yutoAb from "../../../../assets/2024_.png";
import { Sns } from "../../common/Sns";
import { slideFadeIn } from "../../common/Keyframs";

export const Top = () => {
  const theme = useTheme();
  const isXs = useMediaQuery(theme.breakpoints.down("md"));

  return (
    <Box
      sx={{
        backgroundColor: purple,
        color: "white",
        minHeight: "100vh",
        display: "flex",
        alignItems: "center",
        // justifyContent: "center",
      }}
    >
      <Stack direction="row" spacing={10}>
        <Box
          component="img"
          src={yutoAb}
          alt="yutoAb"
          sx={{
            width: "300px",
            height: "auto",
            objectFit: "contain",
          }}
        />
        <Stack
          spacing={5}
          sx={{
            animation: `${slideFadeIn} 1s ease-out`,
          }}
        >
          <Typography variant={isXs ? "h4" : "h2"}>
            Yuto Abe /  
          </Typography>
          <Box
            sx={{
              width: "300px",
              height: "auto",
              objectFit: "contain",
            }}
          >
            <Sns />
          </Box>
          <Box>
            <div>  </div>
            <br />
            <div>
              
              
            </div>
            <div>
              
            </div>
            <br />
            <div>
              WEB
            </div>
            <div>
               ReactTypeScript Scala
            </div>
            <br />
            <div></div>
            <div>
              
            </div>
          </Box>
        </Stack>
      </Stack>
    </Box>
  );
};
